.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 2s linear;
          animation: App-logo-spin infinite 2s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.imgDiv {
  width: 30%;
  float: left;
  margin: 0 20px 0 0;
  border: 1px dotted chocolate;
}

.oneBlock {
  width: 100%;
  display: inline-block;
  padding: 10px 20px;
  margin: 10px 20px;
  border: 2px solid darkblue;
  border-radius: 10px;
}
p {
  margin: 0;
  padding: 0;
  text-align: justify;
}
input {
  margin: 20px 0 0 0;
}
.allBlocks {
  width: 50%;
  margin: auto;
}
li {
  display: inline;
  font-size: 30px;
  padding: 0 10px;
  margin: 0 10px;
  border: 1px solid black;
  cursor: pointer;
}



